*, *::after, *::before {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body, p {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-weight: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

nav, ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

li {
  text-decoration: none;
}

input {
  border-color: transparent;
  outline: none;
  opacity: 1;
  color: #000000;
}
input::placeholder {
  opacity: 1;
}

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  src: url("../fonts/static/OpenSans-Regular.ttf");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  src: url("../fonts/static/OpenSans-Bold.ttf");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 600;
  src: url("../fonts/static/OpenSans-SemiBold.ttf");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  src: url("../fonts/static/OpenSans-Light.ttf");
}
body {
  font-family: "Open Sans";
  color: #787878;
  font-size: 16px;
}
@media (max-width: 980px) {
  body {
    font-size: 14px;
  }
}

.container {
  max-width: 1220px;
  padding: 0 15px;
  margin: 0 auto;
}

.placeholder {
  width: 100%;
  height: 100px;
}
@media (max-width: 720px) {
  .placeholder {
    height: 70px;
  }
}

.header {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  background-color: #ffffff;
}
.header__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 980px) {
  .header__inner {
    flex-direction: column;
  }
}
.header__content {
  padding: 10px 0;
  display: flex;
  align-items: center;
}
@media (max-width: 980px) {
  .header__content {
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 720px) {
  .header__content {
    padding: 5px 0;
  }
}
.header__logo, .header__logo img {
  width: 80px;
  height: 80px;
}
@media (max-width: 720px) {
  .header__logo, .header__logo img {
    width: 60px;
    height: 60px;
  }
}
.header::after {
  content: "";
  height: 15px;
  width: 100%;
  position: absolute;
  bottom: -15px;
  background-image: linear-gradient(to top, transparent, #cacaca);
}

.menu__btn {
  display: none;
}
@media (max-width: 980px) {
  .menu__btn {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    width: 42px;
    height: 32px;
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: #337ab7;
    background-image: url("../images/icons/menu-icon.svg");
    background-size: cover;
    position: relative;
  }
}
.menu__list {
  display: flex;
}
@media (max-width: 980px) {
  .menu__list {
    display: none;
  }
  .menu__list.menu__list_enabled {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 15px;
    align-items: center;
  }
}
.menu__item {
  padding: 15px 0;
}
@media (max-width: 980px) {
  .menu__item {
    padding: 15px 15px;
    font-size: 14px;
    line-height: 14px;
  }
}
.menu__item-link {
  padding: 15px 20px;
  font-size: 16px;
  line-height: 16px;
}
.menu__item:hover {
  background-color: #eeeeee;
  color: #337ab7;
}
.menu__item.apply-link {
  background-color: #337ab7;
  color: #ffffff;
  border-radius: 25px;
}
.menu__item.apply-link:hover {
  background-color: #3498db;
}

.enroll {
  background-color: #f7f7f7;
}
.enroll__inner {
  display: flex;
  column-gap: 30px;
  justify-content: flex-start;
  align-items: flex-end;
  flex: 1;
  padding-top: 50px;
}
@media (max-width: 720px) {
  .enroll__inner {
    flex-direction: column;
  }
}
.enroll__left {
  text-align: center;
  margin-bottom: 90px;
}
@media (max-width: 720px) {
  .enroll__left {
    margin-bottom: 40px;
  }
}
.enroll__title {
  margin-bottom: 40px;
  font-size: 33px;
  line-height: 43px;
  color: #333333;
  font-weight: 600;
}
@media (max-width: 980px) {
  .enroll__title {
    font-size: 28px;
    line-height: 38px;
  }
}
@media (max-width: 720px) {
  .enroll__title {
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 20px;
  }
}
.enroll__description {
  font-weight: 300;
  padding: 0 5px;
  font-size: 18px;
  line-height: 36px;
}
@media (max-width: 980px) {
  .enroll__description {
    font-size: 16px;
    line-height: 30px;
  }
}
@media (max-width: 720px) {
  .enroll__description {
    font-size: 14px;
    line-height: 30px;
  }
}
@media (max-width: 440px) {
  .enroll__description {
    line-height: 25px;
  }
}
.enroll__right {
  min-width: 50%;
}
@media (max-width: 720px) {
  .enroll__right {
    width: 100%;
  }
}
.enroll__form {
  display: flex;
  flex-direction: column;
}
.enroll__form-title {
  color: #333333;
  margin-bottom: 20px;
  font-size: 21px;
  font-weight: 600;
}
.enroll__form-label {
  display: inline;
  position: relative;
}
.enroll__form-label a {
  color: #337ab7;
}
.enroll__form-label a:hover {
  color: #3498db;
}
.enroll__form-label.required::after {
  content: "";
  position: absolute;
  width: 7px;
  height: 7px;
  top: 40%;
  right: -10px;
  background-image: url("../images/icons/required.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.enroll__form-label[for=confidentional-policy] {
  font-weight: 300;
}
.enroll__form-input {
  margin: 5px 0 10px;
  padding-left: 12px;
  border: 1px solid #bababa;
  border-radius: 3px;
  font-size: 12px;
  line-height: 14px;
  color: #999999;
}
.enroll__form-input[type=text], .enroll__form-input[type=tel], .enroll__form-input[type=email] {
  height: 50px;
  width: 100%;
}
.enroll__form-input[type=text]:focus, .enroll__form-input[type=tel]:focus, .enroll__form-input[type=email]:focus {
  border-color: #66afe9;
  box-shadow: 0 0 2px 2px rgba(102, 175, 233, 0.5);
}
.enroll__form-input::placeholder {
  font-family: "Open Sans";
}
.enroll__form-input[type=checkbox] {
  margin-bottom: auto;
}
.enroll__form-submit-btn {
  width: 62%;
  align-self: center;
  padding: 10px 0;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #337ab7;
  border-radius: 25px;
  margin-bottom: 30px;
  font-size: 14px;
}
@media (max-width: 440px) {
  .enroll__form-submit-btn {
    font-size: 12px;
  }
}
.enroll__form-submit-btn:hover {
  background-color: #3498db;
  cursor: pointer;
}
@media (max-width: 357px) {
  .enroll__form-field.confidentional-policy-field {
    display: flex;
    align-items: center;
    column-gap: 10px;
    margin-bottom: 15px;
  }
  .enroll__form-field.confidentional-policy-field input {
    margin: 0;
  }
  .enroll__form-field.confidentional-policy-field label::after {
    right: 52%;
    top: 25px;
  }
}

.site-building__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 0;
}
.site-building__title {
  font-size: 30px;
  font-weight: 400;
  margin: 60px 0 40px;
  color: #333333;
}
@media (max-width: 440px) {
  .site-building__title {
    font-size: 24px;
  }
}
.site-building__content {
  position: relative;
  display: flex;
  width: 100%;
  margin: 0 30px 95px;
  flex-direction: column;
}
.site-building__content-item {
  min-width: 0;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.site-building__content-item h4 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 44px;
  margin-top: 20px;
  font-size: 22px;
  line-height: 24px;
}
@media (max-width: 850px) {
  .site-building__content-item h4 {
    font-size: 20px;
    line-height: 22px;
  }
}
.site-building__content-item hr {
  margin: 20px 0 30px;
  width: 100%;
  color: #3498db;
}
.site-building__content-description {
  font-weight: 300;
  line-height: 28px;
}
.slick-track {
  display: flex;
  padding: 0;
}
.slick-list {
  overflow: hidden;
  min-width: 100%;
  padding: 0 !important;
}
.slick-dots {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: inherit;
  bottom: -50px;
}
.slick-dots li.slick-active button {
  background-color: #666666;
}
.slick-dots button {
  font-size: 0;
  width: 12px;
  height: 12px;
  background-color: #b3b3b3;
  border-radius: 50%;
  margin: 0 7px;
  border: none;
  outline: none;
}

.statistics {
  background-color: #3498db;
  color: #ffffff;
}
.statistics__items {
  padding: 80px 0 60px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
@media (max-width: 720px) {
  .statistics__items {
    padding: 40px 0 20px;
    grid-template-columns: repeat(2, 1fr);
  }
}
.statistics__item {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.statistics__item-icon {
  width: 63px;
  height: 45px;
  margin-bottom: 30px;
}
.statistics__item-value {
  font-size: 21px;
  line-height: 21px;
  margin-bottom: 20px;
}
.statistics__item-description {
  font-weight: 300;
}
@media (max-width: 440px) {
  .statistics__item-description {
    font-size: 12px;
  }
}